*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  min-width: 360px;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  position: relative;
  line-height: 1.5;
  background: #3b4252;
  padding: 0;
}

::-webkit-scrollbar {
  width: 13px;
  padding-top: 80px;
}

::-webkit-scrollbar-track {
    background: #2E3440;
}

::-webkit-scrollbar-thumb {
  background: #4C566A;
}

a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

.scroll-lock {
  overflow: hidden;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* App.js styles */

.App {
  font-size: 50px;
  font-weight: 800;
  height: 700px;
  display: grid;
  place-items: center;
  background: #2e3440;
}

/* Header styles */

.header {
  width: 100%;
  height: 50px;
  background: #2e3440;
  display: grid;
  justify-content: center;
  grid-template: 1fr / 75%;
  position: relative;
  font-family: "Roboto Mono";
}

.header-container {
  height: 100%;
  display: grid;
  align-content: center;
  grid-template: 1fr / 1fr 1fr;
}

.header-title {
  color: #e5e9f0;
  font-size: 25px;
  font-weight: 800;
  display: inline-block;
  height: 100%;
  line-height: 50px;
  left: 0;
  justify-self: start;
  cursor: pointer;
  transition: 0.2s linear;
}

.header-title:hover {
  text-shadow: 0 0px 5px #e5e9f0;
}

.header-searcher {
  width: 250px;
  position: relative;
  height: 30px;
  justify-self: end;
  align-self: center;
  overflow: hidden;
  border-radius: 10px;
}

.header-searcher:focus {
  border: 5px solid #fff;
}

#search {
  width: 100%;
  height: 100%;
  border-radius: 30px;
  border: 0;
  background: #e5e9f0;
  color: #2e3440;
  padding: 0 38px 0 15px;
  font-weight: 800;
  position: absolute;
  left: 0;
}

#search:focus {
  outline: none;
}

.search-icon_box {
  width: 32px;
  height: 30px;
  cursor: pointer;
  display: grid;
  position: absolute;
  padding-right: 2px;
  right: 0;
  top: 0;
  grid-template: 1fr / 1fr;
  place-items: center;
}

.search-icon {
  display: inline;
  width: 20px !important;
  height: 20px;
  top: 0;
  right: 5px;
  line-height: 30px;
  color: #2e3440;
  text-align: center;
  cursor: pointer;
}

@media (max-width: 590px) {
  .header {
    height: 60px;
    grid-template: 1fr / 1fr;
    padding: 0 20px;
  }
  .header-title {
    line-height: 60px;
  }
  .header-searcher {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #e5e9f0;
    transition: 0.2s linear;
  }
  .header-searcher:hover {
    box-shadow: 0 0 5px 1px #e5e9f0;
  }
  #search {
    transform: translate(100%, 0);
    display: none;
  }
  .search-icon {
    width: 34px;
    height: 34px;
    left: 0;
    top: 0;
    line-height: 34px;
  }
}

.searcher_active {
  width: 200px;
  border-radius: 30px;
}

.searcher_active #search {
  display: block;
  transform: translate(0, 0);
}

.searcher_active:hover {
  box-shadow: none;
}

/* The end of header styles */

/* Main styles */

main {
  width: 100%;
  height: auto;
  padding: 30px 12.5%;
  font-family: "Roboto Mono";
  position: relative;
}

mark {
  background: #8fbcbb;
  color: #f8f9fb;
  padding: 1px;
  border-radius: 2px;
}

.main-container {
  width: 100%;
  height: auto;
  min-height: 500px;
  padding: 10px;
  border-radius: 3px;
  background: #434c5e;
  position: relative;
}

.preloader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #e5e9f099;
  z-index: 20;
  border-radius: 5px;
  display: grid;
  grid-template: 50px 50px / 1fr;
  align-items: center;
  justify-content: center;
  grid-row-gap: 30px;
}

.preloader-gif {
  left: 0;
  top: 0;
  width: 400px;
  height: auto;
  margin: 50px auto 0 auto;
}

.preloader-title {
  color: #434c5e;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
}

.main-item {
  width: 100%;
  height: 100%;
  padding: 20px 10px 0 10px;
  font-weight: 600;
}

.none-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin: 20px 0;
}

/* TP-Styles */

.tp-title {
  font-size: 25px;
  color: #e5e9f0;
}

.tp-link {
  font-size: 16px;
  margin-right: 20px;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-link:hover {
  text-shadow: 0 0 8px #e5e9f0;
}

.tp-link svg {
  margin-right: 5px;
}

.tp-image_block {
  margin: 30px 0 40px 0;
}

.tp-image {
  border-radius: 8px;
  width: 100%;
}

.tp-text {
  font-size: 18px;
  color: #d8dee9;
  white-space: pre-line;
}

.tp-sublinks_block {
  width: 100%;
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  place-items: center;
  margin-top: 10px;
}

.tp-sublink {
  color: #d8dee9;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-sublink:hover {
  text-shadow: 0 0 8px #d8dee9;
}

.tp-sublink::before {
  content: "#";
  display: inline-block;
  margin-right: 4px;
}

/* TP-Code styles */

.tp-code_block {
  width: 100%;
  height: auto;
  display: block;
}

.tp-code_container {
  width: 100%;
  height: auto;
}

.tp-code_box {
  padding: 26px 5px 5px 5px;
  background: #3b4252;
  border-radius: 8px;
  position: relative;
  border: 2px solid #4c566a;
  box-shadow: 0px 0px 8px 0 #4c566a;
  margin-bottom: 20px;
}

.tp-code_list {
  display: block;
  border-radius: 5px;
  width: 100%;
  background: transparent;
  padding: 10px 10px 20px 10px;
  overflow-x: hidden;
}

/* TP-CODE HIGHLIGHTED STYLES */

.tp-code pre {
  color: #d8dee9;
}

.tp-code-white {
  color: #d8dee9;
}

.tp-code-blue {
  color: #76a0c1;
}

.tp-code-purple {
  color: #b48ead;
}

.tp-code-green {
  color: #a3be8c;
}

.tp-code-yellow {
  color: #ebcb8b;
}

.tp-code-orange {
  color: #d08770;
}

.tp-code-ocean {
  color: #7fbabb;
}

.tp-code-gray {
  color: rgb(106, 116, 136);
}

.dots {
  position: absolute;
  left: 8px;
  top: 8px;
  display: grid;
  width: 52px;
  grid-template: 1fr / repeat(3, 1fr);
  grid-gap: 8px;
}

.dot-red,
.dot-green,
.dot-yellow {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s linear;
}

.dot-red {
  background: #bf616a;
}

.dot-red:hover {
  box-shadow: 0 0 5px 0.5px #bf616a;
}

.dot-green {
  background: #a3be8c;
}

.dot-green:hover {
  box-shadow: 0 0 5px 0.5px #a3be8c;
}

.dot-yellow {
  background: #ebcb8b;
}

.dot-yellow:hover {
  box-shadow: 0 0 5px 0.5px #ebcb8b;
}

.tp-code {
  list-style: none;
  font-size: 16px;
  color: #88c0d0;
  font-weight: 600;
  line-height: 20px;
  width: 100%;
}

.tp-code pre {
  line-height: 5px;
  font-weight: 600;
}

.tp-code_block {
  margin: 30px 0;
}

.result_list {
  margin-bottom: 40px;
}

.result-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #8fbcbb;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tab {
  display: block;
  margin-left: 20px;
  line-height: 10px;
}

.double-tab {
  display: block;
  margin-left: 40px;
  line-height: 10px;
}

.tp-code_list {
  border-radius: 5px 5px 0 0;
}

.result_list {
  border-radius: 0 0 5px 5px;
}

.result::before {
  content: ">  ";
  color: #ebcb8b;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.result pre {
  display: inline;
}

/* TP-CODE-STATE-BAR STYLES */
.tp-code_state-bar {
  display: grid;
  height: 20px;
  width: 100%;
  overflow: hidden;
  position: relative;
  bottom: 20px;
  left: 0;
  grid-template: 1fr/ 1fr 8fr 1fr 2fr;
  font-size: 16px;
  line-height: 20px;
  border-radius: 2px;
}

.tp-code_mode {
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  height: 100%;
  width: 100%;
  background: #88c0d0;
  color: #3b4252;
}

.tp-code_main-info {
  background: #4c566a;
  display: grid;
  grid-template: 1fr / repeat(2, 1fr);
}

.tp-code_buffer-name {
  margin-left: 10px;
  color: #eceff4;
}

.tp-code_extension-name {
  position: relative;
}

.tp-code_extension-name span {
  position: absolute;
  right: 35px;
  color: #eceff4;
  top: -1px;
}

.tp-code_extension-name svg {
  position: absolute;
  right: 10px;
  width: 20px;
  height: 20px;
}

.tp-code_system-type {
  position: relative;
  background: #81a1c1;
}

.tp-code_bar-type {
  display: none;
}

.tp-code_system-type span {
  margin-left: 10px;
  color: #3b4252;
}

.tp-code_system-type img {
  width: 18px;
  height: 18px;
  position: absolute;
  right: 10px;
  top: 1px;
}

.tp-code_buffer-info {
  background: #88c0d0;
  display: grid;
  color: #4c566a;
  grid-template: 1fr / repeat(3, 1fr);
}

.buffer-procents {
  padding-left: 10px;
}

.buffer-symbol-number {
  text-align: right;
  padding-right: 10px;
}

/* TP-THEMES STYLES */

.tp-themes-block {
  border-left: 3px solid #88c0d0;
}

.tp-themes-block ul {
  list-style-type: none;
}

.tp-themes-list {
  padding-left: 0px;
  position: relative;
  margin-left: -2.65px;
}

.tp-themes-link {
  position: relative;
  display: inline-block;
  margin-left: 10px;
}

.tp-themes-arrow {
  position: absolute;
  right: -30px;
  top: 0.3em;
}

.tp-themes-link::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  display: block;
  transform: translate(-10px, 0.8em);
}

.tp-themes-sublist {
  display: block;
  margin: 11px 0 40px 20px;
  padding: 0 0 0 20px;
  border-left: 2px solid #88c0d0;
  position: relative;
}

.tp-themes-sublist::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 0;
  left: -20px;
}

.tp-themes-subitem {
  position: relative;
  padding-left: 5px;
}

.tp-themes-sublink {
  position: relative;
  display: inline-block;
}

.tp-themes-subitem::before {
  content: "";
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: -20px;
}

.tp-themes-sublink:hover,
.tp-themes-link:hover {
  padding-left: 20px;
  transition: 0.2s linear;
  color: #8fbcbb;
}

.tp-themes-link:hover {
  color: #b48ead;
}

.tp-themes-link:hover::before {
  left: 0px;
}

.tp-themes-sublist::before,
.tp-themes-subitem::before,
.tp-themes-link::before {
  background: #88c0d0;
}

/* TP-CODE BUFFER STYLES */

.tp-code_buffer-bar {
  background: #4c566a;
  border-radius: 3px;
  display: grid;
  grid-template: 20px / repeat(3, 135px) auto 100px;
  height: 20px;
  margin-top: 5px;
  overflow: hidden;
  padding: 0 0 0 10px;
  width: 100%;
}

.tp-code_buffer {
  font-size: 13px;
  height: 20px;
  line-height: 20px;
  text-align: left;
  width: 100%;
  position: relative;
  color: #eceff4;
}

.tp-code_buffer .tp-code_buffer--name {
  padding-left: 10px;
}

.tp-code_buffer.buffer_active .tp-code_buffer--name {
  color: #4c566a;
}

.tp-code_buffer:first-child svg {
  color: #81a1c1;
}

.tp-code_buffer::before {
  content: "";
  height: 16px;
  width: 2px;
  position: absolute;
  left: 0;
  top: 2px;
  background: #eceff4;
}

.tp-code_buffer::after {
  content: "";
  height: 16px;
  width: 2px;
  position: absolute;
  right: -2px;
  top: 2px;
  background: #eceff4;
}

.tp-code_buffer.buffer_active {
  background: #81a1c1;
  color: #2e3440 !important;
}

.buffer_active::before,
.buffer_active::after {
  display: none;
}

.tp-code_buffer.buffer-title {
  background: #81a1c1;
  color: #2e3440;
  display: block;
  text-align: center;
}

.tp-code_buffer.buffer-title::before,
.tp-code_buffer.buffer-title::after {
  display: none;
}

.tp-code_buffer.buffer-title span {
  margin-left: 0;
}

.tp-code_buffer svg,
.tp-code_buffer img {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 2.5px;
  display: inline-block;
  margin-left: 10px;
}

.buffer_active svg {
  fill: #4c566a;
  top: 3px;
}

/* Pagination styles */

.tp-pagination_block {
  display: grid;
  height: 70px;
  width: 100%;
  grid-template: 30px 30px / 50% 50%;
  grid-gap: 10px;
  margin: 20px 0;
  padding-right: 15px;
  z-index: 6;
}

.tp-pagination_button {
  width: 100%;
  height: 100%;
  background: #8fbcbb;
  border-radius: 5px;
  display: grid;
  font-weight: 800;
  align-content: center;
  text-align: center;
  transition: 0.2s linear;
  cursor: pointer;
  z-index: 10;
}

.tp-pagination_button.next-button {
  grid-row: 0 / 1;
  grid-column: 2 / 3;
}

.tp-pagination_button:hover {
  background: #3b4252;
  color: #8dbab9;
  box-shadow: 0 0 7px 2px #8fbcbb;
}

.tp-pagination_button span {
  display: inline-block;
  color: #434c5e;
  width: 100%;
  height: 30px;
  line-height: 29px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tp-pagination_button span:hover {
  color: #8dbab9;
}

.tp-page-searcher {
  display: grid;
  grid-template: 30px / 50% 50%;
  grid-column-gap: 10px;
  grid-column: 1 / 3;
  grid-row: 0 / 1;
  padding-right: 10px;
}

.tp-page-searcher_button {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background: #81a1c1;
  color: #e5e9f0;
  display: grid;
  place-items: center;
  cursor: pointer;
  transition: 0.2s linear;
  z-index: 10;
}

.tp-page-searcher_button a {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 30px;
}

.tp-page-searcher_button:hover {
  background: #3b4252;
  box-shadow: 0 0 5px 2px #81a1c1;
}

#pageSearcher {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  border: 0;
  background: #e5e9f0;
  padding: 0 10px;
  color: #3b4252;
}

#pageSearcher:focus {
  outline: none;
}

.tp-page-id {
  color: #e5e9f0;
  position: absolute;
  right: 20px;
  top: 10px;
}

