/* Compact menu styles */

menu {
  padding: 0;
  margin: 0 0 20px 0;
  font-family: "Roboto Mono", monospace;
  position: static;
  top: 0px;
}

.compact-menu {
  display: none;
  width: 100%;
  height: 50px;
  background: #434c5e;
  border-radius: 3px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.compact-menu_title {
  width: 100%;
  height: 100%;
  line-height: 50px;
  font-size: 25px;
  color: #e5e9f0;
  text-align: center;
  letter-spacing: 0.2em;
  transition: 0.2s linear;
}

.compact-menu_title:hover {
  background: #3b4252;
  color: #d8dee9;
  text-shadow: 0 0 5px #d8dee9;
}

.compact-menu_curtain {
  display: none; /* grid */
  position: absolute;
  background: rgba(46, 52, 64, 0.95);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  width: 100%;
  top: -55px;
  left: 0;
  place-items: center;
}

.curtain_active {
  display: grid;
  z-index: 10;
}

.compact-menu_list {
  list-style: none;
  position: relative;
  width: 100%;
  height: auto;
  display: grid;
  grid-template: repeat(4, 1fr) / 1fr;
  padding-left: 0;
  place-content: center;
  grid-gap: 10px;
}

.compact-menu_item {
  height: 50px;
  width: 100%;
  height: 100%;
  display: grid;
  height: 60px;
  text-align: center;
  align-items: center;
}

.compact-menu_link {
  width: 100%;
  height: 100%;
  line-height: 60px;
  font-size: 25px;
  text-shadow: 0 0 8px #e5e9f0;
  transition: 0.2s linear;
}

.compact-menu_link::before {
  margin-right: 15px;
}

.compact-menu_link:hover {
  background: rgba(44, 50, 62, 1);
}

.compact-menu_cross {
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  z-index: 0;
  right: 20px;
  top: 20px;
  cursor: pointer;
  transition: 0.3s linear;
}

.compact-menu_cross::before {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  top: 0;
  left: 0;
  margin-left: 50%;
  transform: rotate(45deg);
}

.compact-menu_cross::after {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  margin: -100% 0 0 50%;
  transform: rotate(-45deg);
}

/* Apply this */
@media (max-width: 590px) {
